@-webkit-keyframes fadeInSmallText {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes fadeInSmallText {
    from {opacity: 0;}
    to {opacity: 1;}
}

@-webkit-keyframes fadeInBigText {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes fadeInBigText {
    from {opacity: 0;}
    to {opacity: 1;}
}

.center {
    text-align: center;
    margin-top:40vh;
    font-family: 'Montserrat', sans-serif;
    color:#725c16;
}

.center:hover {
    color:#FFFFFF;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s; 
}

h1 {
    font-size:4em;
    top:4em;
}

#smallTitleFadeIn {
    opacity: 0;
    -webkit-animation-name: fadeInSmallText;
            animation-name: fadeInSmallText;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}

#bigTitleFadeIn {
    opacity: 0;
    -webkit-animation-name: fadeInBigText;
            animation-name: fadeInBigText;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}


